.navbar-wrapper {
  background-color: rgb(59, 57, 57);
  max-width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
}

.navbar-main {
  width: 200px;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.navbar-main a {
  text-decoration: none;
  color: white;
  font-family: "Lato";
  font-weight: 300;
  font-style: bold;
  margin-left: 10px;
}

.navbar-main__profile-main {
  background-color: green;
  height: 100%;
  width: 50px;
  margin-right: 10px;
  overflow: hidden;
}
.navbar-main__profile-main img {
  height: 100%;
  width: 100%;
}

.login-main {
  background-color: red;
  height: 100%;
  width: 100%;
}
.login-main form {
  background-color: yellow;
}

.homepage-main {
  background-color: transparent;
  max-height: 100%;
}
.homepage-main .test {
  background-color: transparent;
  height: 500px;
}
.homepage-main .homepage-main__form-wrapper {
  height: 100px;
}
.homepage-main .homepage-main__form-wrapper form {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.homepage-main .homepage-main__form-wrapper form .homepage-main__filterscontainer {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 10px;
  width: 100%;
  font-family: "Lato";
  display: flex;
  justify-content: center;
}
.homepage-main .homepage-main__form-wrapper form .homepage-main__filterscontainer .filterscontainer__wrapper {
  width: 600px;
  display: flex;
  justify-content: space-between;
}
.homepage-main .homepage-main__form-wrapper form .homepage-main__filterscontainer .filterscontainer__wrapper .filterscontainer__smallscreen {
  display: none;
}
@media screen and (max-width: 470px) {
  .homepage-main .homepage-main__form-wrapper form .homepage-main__filterscontainer .filterscontainer__wrapper .filterscontainer__smallscreen {
    display: block;
  }
}
.homepage-main .homepage-main__form-wrapper form .homepage-main__filterscontainer .filterscontainer__wrapper .filterscontainer__bigscreen {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 470px) {
  .homepage-main .homepage-main__form-wrapper form .homepage-main__filterscontainer .filterscontainer__wrapper .filterscontainer__bigscreen {
    display: none;
  }
}
.homepage-main .homepage-main__form-wrapper form .homepage-main__filterscontainer .filterscontainer__wrapper input {
  max-width: 20px;
  outline: none;
  border: 1px solid lightgray;
  text-align: center;
}
@media screen and (max-width: 470px) {
  .homepage-main .homepage-main__form-wrapper form .homepage-main__filterscontainer .filterscontainer__wrapper .filterslists {
    display: none;
  }
}
.homepage-main .homepage-main__form-wrapper form .homepage-main__filterscontainer .filterscontainer__wrapper .filterslists span {
  margin: 10px;
}
.homepage-main .homepage-main__form-wrapper form .homepage-main__filterscontainer .filterscontainer__wrapper .filterslists select {
  outline: none;
  border: 1px solid lightgray;
  text-align: center;
}
.homepage-main .homepage-main__form-wrapper form .homepage-main__searchcontrolwrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}
.homepage-main .homepage-main__form-wrapper form .homepage-main__searchcontrolwrapper input {
  height: 30px;
  width: 500px;
  border: none;
  outline: 1px solid lightgray;
  padding: 10px;
  margin-left: 10px;
}
.homepage-main .homepage-main__form-wrapper form .homepage-main__searchcontrolwrapper button {
  height: 30px;
  width: 100px;
  border: none;
  background-color: rgb(18, 75, 18);
  color: white;
  margin-right: 10px;
}
.homepage-main .homepage-main__showcase {
  display: grid;
  place-content: center;
}
.homepage-main .homepage-main__showcase h1 {
  padding: 10px;
  font-family: "Lato";
}
.homepage-main .homepage-main__showcase h2 {
  padding: 10px;
  font-family: "Lato";
}
.homepage-main .homepage-main__showcase .showcase__images-grid img {
  margin: 10px;
}

.wrapper {
  background-color: rgba(255, 255, 255, 0.5);
  filter: blur(50px);
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  left: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  width: 100vw;
  max-width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-main {
  height: 100vh;
  width: 100vw;
  max-width: 100%;
}/*# sourceMappingURL=style.css.map */