.navbar-wrapper{
    background-color: rgb(59, 57, 57);
    max-width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
}

.navbar-main{
    width: 200px;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    a{
        text-decoration: none;
        color: white;
        font-family: 'Lato';
        font-weight: 300;
        font-style: bold;
        margin-left: 10px;
    }
}

.navbar-main__profile-main{
    background-color: green;
    height: 100%;
    width: 50px;
    margin-right: 10px;
    overflow: hidden;

    img{
        height: 100%;
        width: 100%;
    }
}